.note {
  background-color: rgb(254, 246, 139);
  border-radius: 10px;
  padding: 1rem;
  min-height: 170px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  white-space: pre-wrap;
}

.noteFooter {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.deleteIcon {
  cursor: pointer;
}
